main nav,
.folwell-vertical-nav {
  padding: 1em;
  background-color: map-get($folwell-palette, off-white);
  ul {
    list-style: none;
    margin: 0;
    padding-left: 1.5em;
    width: 100%;
    background: map-get($folwell-palette, off_white);
    > li {
      padding: 0;
    }
    a,
    span {
      font-size: .833em;
      color: $base-font-color;
      display: block;
      border-bottom: none;
      padding: .5em;
      position: relative;
    }
    a {
      &.is-active {
        background: map-get($folwell-palette, gray-light);
        color: $base-font-color;
        font-weight: 700;
      }
      &:hover {
        color: map-get($folwell-dark-palette, maroon);
        background: map-get($folwell-dark-palette, gray-lighter);
      }
      &:focus {
        background: transparent;
      }
    }
  }
  //.is-active {
  //  background: map-get($folwell-palette, gray-light);
  //  color: $base-font-color;
  //  font-weight: 700;
  //  &:hover {
  //    background: map-get($folwell-palette, gray-light);
  //    color: map-get($folwell-dark-palette, maroon);;
  //  }
  //}
  .menu-item--expanded,
  .menu-item--collapsed {
    list-style-type: none;
    list-style-image: none;

  }

  .menu-item--expanded > a:after {
    font-family: 'Font Awesome 5 Free';
    font-size: inherit;
    font-weight: 600;
    content: "\f107";
    line-height: 1;
    position: absolute;
    text-indent: 0;
    margin-top: 0.5em;
    margin-left: 0.8em;
    right: 2em;
  }
}
